* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  body {
    background-color: #7f53ac;
    background-image: linear-gradient(315deg, #7f53ac 0%, #647dee 74%);

    color: white;
    font-family: "Poppins", sans-serif;
    min-height: 100vh;
  }
  header {
    font-size: 1.7rem;
  }
  
  header,
  form {
    min-height: 25vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }


/*div glow work custom*/

.glow {
     
    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 100px 80px rgba(15, 243, 118, 0.26)
  ;

}
.glow:hover {
    background-image: linear-gradient(315deg, #36096d2d 10%, #37d6d6a4 74%);    
}

.block {
   
    color: white;
    padding: 1% 3%;
    border-radius: 60px;
    font-size: 1.5rem;
   

}


/*glow work done */

  form input,
  form button {
    padding: 0.5rem;
    font-size: 2rem;
    border: none;

    background-color: #36096d2d;
    background-image: linear-gradient(315deg, #36096d2d 0%, #37d6d6a4 74%);        
        
  }
  form button {
    color: #ff6f47;
    background: #f7fffe;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  form button:hover {
    background-color: #1f86a5;
    color: white;
  }
  
  .todo-input {
    border-radius: 30px 0 0 30px;
    min-width: 33%;
    color: white;
    padding-left: 20px; 
  }

  .todo-button {
    margin: 0 1% 0 0 ;
    border-radius: 0 30px 30px 0 ;
    padding-right: 1rem;
    
    background-color: #36096d2d;
    background-image: linear-gradient(315deg, #36096d2d 0%, #37d6d6a4 74%);        

  }
  .fas {
      color: white;
  }

  .todo-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .todo-list {
 
    min-width: 30%;
    list-style: none;
  }
  
  .todo {
    border-radius: 30px;
    margin: 1rem;
    background: white;
    font-size: 1.5rem;
    color: black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 1s ease;

    background-color: #36096d2d;
    background-image: linear-gradient(315deg, #36096d2d 0%, #37d6d6a4 74%);        

  }


  .filter-todo {
    border-radius: 30px;
    padding: 1rem;
    color:white;
    background-color: #36096d2d;
    background-image: linear-gradient(315deg, #36096d2d 0%, #37d6d6a4 74%);        

  }
  .todo li {
    flex: 1;
  }
  
  .trash-btn,
  .complete-btn {
    background: rgba(1, 238, 206, 0.226);
    color: rgb(245, 245, 245);   
    border: none;
    padding: 0.6rem;
    cursor: pointer;
    font-size: 1rem;
    border-radius: 20px;    
    margin-right: 3%;

    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.336),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06);
  }

  

  .todo-item {
    padding: 0.6rem 1rem;
    color:white;
  }

  
  .fa-trash,
  .fa-check {
    pointer-events: none;

  }
  
  .fall {
    transform: translateY(10rem) rotateZ(20deg);
    opacity: 0;
  }
  
  .completed {
    text-decoration: line-through;
    opacity: 0.5;
  }
  
  /*CUSTOM SELECTOR */
  select {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    outline: 0;
    box-shadow: none;
    border: 0 !important;
    background-image: none;
  }
  
  /* Custom Select */
  .select {
    margin: 1rem;
    position: relative;
    
  }
  select {
    color: white;
    font-family: "Poppins", sans-serif;
    cursor: pointer;
    width: 12rem;
  }
  /* Arrow */
  .select::after {
    border-radius: 10px 20px 20px 10px;
    margin-left: -15%;
    content: ">";
    padding: 0.4rem;
    
    cursor: pointer;
    pointer-events: none;
  }
